import { bulletInstance } from '../controller/index'
// 炮弹类
class Bullet {
  constructor(data, options = {}) {
    this.data = data
    this.type = options.type || 1
    // 装载炮弹的集合
    this.bullets = []
  }
  // 安装炮弹
  installBullte = () => {
    bulletInstance.installBullte(this.data, this.type, this.bullets)
  }
  // 添加炮弹
  addBullte (bullte) {
    this.bullets.push(bullte)
  }
  // 移除炮弹
  removeBullte (bullte) {

  }
}
export default Bullet